import * as yup from 'yup';

import DataStore from './DataStore';

class ValidationSamples {
    fieldsRegSample = [ 
        {name: "secondName", nameForLegend: "", type: 'text'}, 
        {name: "firstName", nameForLegend: "", type: 'text'},
        {name: "login", nameForLegend: "", type: 'text'}, 
        {name: "password", nameForLegend: "", type: 'password'}, 
        {name: "email", nameForLegend: " ", type: 'email'}
    ]

    fieldsLoginSample   = [ 
        {name: "login", nameForLegend: "", type: 'text'}, 
        {name: "password", nameForLegend: "", type: 'password'}
    ]

    regDataSample = {
        firstName: '',
        secondName: '',

        login: '',
        password: '',
    
        email: ''
    }

    loginDataSample = {
        login: '',
        password: ''
    }


    loginSchema = yup.object().shape({
        login: yup
            .string()
            .required("   "),

        password: yup
            .string()
            .required("   "),

    })

    regSchema = yup.object().shape({
        firstName: yup
            .string()
            .required("   ")
            .matches(/^([^0-9]*)$/, "    "),

        secondName: yup
            .string()
            .required("   ")
            .matches(/^([^0-9]*)$/, "    "),

        login: yup
            .string()
            .min(8, "     8 ")
            .max(16, "     16 ")
            .test('isAvailableLogin', ' ', 
                function(login) {
                    const matches =  DataStore.usersFromLocal.find(user => user.login === login)

                    return !matches
            })
            .required("   "),

        password: yup
            .string()
            .min(8, "     8 ")
            .max(24, "     24 ")
            .required("   "),

        email: yup
            .string()
            .email("  ")
            .required("   "),
    })
}

export default new ValidationSamples()